\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import '@/scss/vars.scss';\n.edit-block {\n  margin-bottom: 100px;\n\n  @media(max-width: 1199px) {\n    margin-bottom: 60px;\n  }\n\n  @media(max-width: 991px) {\n    margin-bottom: 20px;\n  }\n\n  $root: &;\n\n  &__row {\n    display: flex;\n    flex-wrap: wrap;\n    margin: 0 -20px;\n    @media(max-width: 1199px) {\n      margin: 0 -10px;\n    }\n  }\n\n  &__item {\n    width: 100%;\n    max-width: calc(50% - 40px);\n    margin: 0 20px;\n    border-radius: 20px;\n    overflow: hidden;\n    padding: 40px 40px;\n    background: $dark;\n    position: relative;\n    font-size: 0;\n    display: flex;\n    flex-direction: column;\n    box-shadow: 7px 7px 30px $shadow-primary;\n    min-height: 200px;\n\n    @media(max-width: 1199px) {\n      padding: 30px;\n      max-width: calc(50% - 20px);\n      margin: 0 10px;\n    }\n\n    @media(max-width: 991px) {\n      max-width: calc(100% - 20px);\n      margin: 0 10px 20px 10px;\n    }\n\n    @media(max-width: 767px) {\n      padding: 20px;\n    }\n\n    @media(max-width: 350px) {\n      min-height: 160px;\n    }\n\n    &:nth-child(2n) {\n      #{$root} {\n        &__item-image {\n          background: #cbcbd0;\n        }\n      }\n    }\n  }\n\n  &__item-image {\n    background: $dark;\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n  }\n\n  &__item-thumb {\n    max-width: none;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    object-position: 50% 50%;\n  }\n\n  &__item-title {\n    font-weight: bold;\n    font-size: 47px;\n    color: #fff;\n    margin-bottom: 43px;\n    position: relative;\n\n    @media(max-width: 1440px) {\n      font-size: 40px;\n      margin-bottom: 30px;\n    }\n\n    @media(max-width: 1199px) {\n      font-size: 30px;\n    }\n\n    @media(max-width: 575px) {\n      font-size: 25px;\n    }\n  }\n\n  &__item-row {\n    margin-top: auto;\n    display: flex;\n    flex-wrap: wrap;\n    align-items: center;\n    justify-content: space-between;\n    position: relative;\n  }\n\n  &__item-link {\n    background: rgba(255, 255, 255, 0.07);\n    padding: 11px 52px 11px 20px;\n    margin-right: 20px;\n    position: relative;\n    color: #FFF;\n    font-weight: 600;\n    font-size: 15px;\n\n    @media(max-width: 1199px) {\n      padding: 10px 50px 10px 20px;\n    }\n\n    @media(max-width: 575px) {\n      font-size: 14px;\n      padding: 10px 30px 10px 15px;\n    }\n  }\n\n  &__item-ico {\n    position: absolute;\n    top: calc(50% - 6px);\n    width: 12px;\n    height: 12px;\n    right: 20px;\n    fill: #fff;\n    transform: rotate(180deg);\n\n    @media(max-width: 575px) {\n      right: 10px;\n    }\n  }\n\n  &__item-label {\n    font-weight: bold;\n    font-size: 18px;\n    color: #fff;\n    background: #FF7A5C;\n    border-radius: 7px;\n    padding: 9px 20px;\n\n    @media(max-width: 575px) {\n      padding: 8px 15px;\n      font-size: 14px;\n    }\n  }\n}\n","$primary: #191919;\n$dark: #41445C;\n$dark2: #53566c;\n$shadow-primary: rgba(65, 68, 92, 0.3);\n$shadow-dark2: rgba(248, 248, 248, 0.2);\n$grey: #818181;\n$light: #c4c4c4;"],"sourceRoot":""}]);
